import React, { useEffect } from 'react';
import { Route, Routes, useNavigate } from 'react-router-dom';

import EditProfile from '../EditProfilePage/EditProfilePage';
import Footer from '../Footer/Footer';
import Header from '../header/Header';
import LoginPage from '../LoginPage/LoginPage';
import Main from '../MainPage/MainPage';
import NotFound from '../NotFoundPage/NotFound';
import SignupPage from '../SignupPage/SignupPage';
import BoardPage from '../BoardPage/BoardPage';
import WelcomePage from '../WelcomePage/WelcomePage';
import Toast from '../Toast/Toast';
import { RequireAuth } from '../../hoc/RequireAuth';

import { getTokenFromLocalStorage } from '../../redux/ColumnSlice';
import { getUserAuth } from '../../redux/apiReducer';
import { getIdFromToken } from '../../redux/EditProfileSlice';
import { useAppDispatch, useAppSelector } from '../../redux/hooks/redux';

import './App.css';

const App = (): JSX.Element => {
  const { isAuth } = useAppSelector((store) => store.user);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    const token = getTokenFromLocalStorage();
    if (token) {
      const userId = getIdFromToken(token);
      dispatch(getUserAuth(userId, token));
    } else {
      if (!isAuth) {
        return navigate('/');
      }
    }
  }, []);

  useEffect(() => {
    if (!isAuth) {
      return navigate('/');
    }
  }, [isAuth]);

  return (
    <div className="body">
      <Header />
      <div className="main-container">
        <Routes>
          <Route path="/" element={<WelcomePage />} />
          <Route path="/*" element={<NotFound />} />
          <Route path="/main" element={<Main />} />
          <Route path="/edit" element={<EditProfile />} />
          <Route
            path="/sign-up"
            element={
              <RequireAuth>
                <SignupPage />
              </RequireAuth>
            }
          />
          <Route
            path="/login"
            element={
              <RequireAuth>
                <LoginPage />
              </RequireAuth>
            }
          />
          <Route path="/board" element={<BoardPage />} />
        </Routes>
      </div>
      <Toast />
      <Footer />
    </div>
  );
};

export default App;
